.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 5px 15px;
  font-size: 12px;
  color: #111114;
  background-color: #f7f7f7;
}

.footer-logo {
  max-width: 98px;
  margin-right: 4px;
}

.footer-author {
  display: inline-block;
  margin-left: 2px;
  border-bottom: 1px solid transparent;
  color: #111114;
  transition: all 500ms ease 0ms;

  &:hover {
    border-bottom-color: #111114;
  }
}
