.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 30;
  display: none;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);

  &.hide {
    display: none;
  }

  &.show {
    display: flex;
  }
}

.modal-container {
  max-width: 480px;
  margin: 15px;
  padding: 15px;
  border-radius: 10px;
  background-color: #f7f7f7;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.3);
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.modal-title {
  font-size: 20px;
  line-height: 20px;
  color: #111114;
}

.modal-button {
  max-width: 20px;
}

.modal-content {
  margin-top: 15px;
}

.modal-text {
  margin-top: 60px;
  font-size: 24px;
  line-height: 24px;
  font-weight: 600;
  text-align: center;
  text-transform: uppercase;
  color: #f24c49;
}
