.table {
  align-self: flex-start;
  width: 100%;
  margin-top: 60px;
  border-collapse: collapse;
  border-radius: 4px;
  background-color: #111114;
}

.table-header .table-cell {
  font-size: 18px;
  line-height: 22px;
  font-weight: normal;
  color: #fff;
}

.table-body {
  background-color: #f7f7f7;
}

.table-row:nth-child(even) {
  background-color: #fff;
}

.table-cell {
  width: 25%;
  padding: 4px;
  font-size: 14px;
  line-height: 22px;
  text-align: left;
  text-transform: uppercase;
  color: #111114;
}

.table-button {
  padding: 4px;
  border-radius: 4px;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  color: #111114;
  background-color: #fff;
}
