.stats {
  @media all and (max-width: 480px) {
    overflow-x: scroll;
  }
}

.stats-link {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 10;
  min-width: 120px;
  padding: 4px;
  border-radius: 4px;
  font-size: 16px;
  line-height: 32px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #111114;
  transform: translate(-60px, -20px);
}
