.toolbar {
  position: sticky;
  top: 0;
  z-index: 20;
  display: flex;
  justify-content: space-between;
  margin-top: 60px;
  border-radius: 4px;
  background-color: #fff;

  @media all and (max-width: 480px) {
    flex-direction: column;
  }
}

.toolbar-counter {
  display: flex;

  @media all and (max-width: 480px) {
    justify-content: center;
  }
}

.toolbar-counter-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 130px;
  padding: 8px 10px;
  border-right: 2px solid #f7f7f7;

  + .toolbar-counter-container {
    @media all and (max-width: 480px) {
      border-right: 0;
    }
  }
}

.toolbar-counter-value {
  font-size: 20px;
  color: #f24c49;
}

.toolbar-counter-legend {
  padding-left: 4px;
  font-size: 18px;
  color: #111114;
}

.toolbar-button {
  min-width: 120px;
  padding: 4px;
  border-radius: 4px;
  font-size: 16px;
  line-height: 32px;
  text-transform: uppercase;
  color: #fff;
  background-color: #111114;
}
