html {
  background-color: #f7f7f7;
}

* {
  box-sizing: border-box;
}

body,
blockquote,
figure,
p,
h1,
h2,
h3,
h4 {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

strong,
h1,
h2,
h3,
h4 {
  font-weight: normal;
}

em,
cite {
  font-style: normal;
}

input,
textarea,
select {
  display: block;
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0;
  font-family: inherit;
  background-image: none;
  background-color: transparent;
  box-shadow: none;
  appearance: none;
  &:focus {
    outline: none;
  }
}

button {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: inherit;
  background-color: transparent;
  cursor: pointer;
  &:focus {
    outline: none;
  }
}
