.form {
  display: flex;
  justify-content: center;
  margin-top: 60px;
}

.form-field {
  width: 100%;
  padding: 4px;
  border-radius: 4px 0 0 4px;
  font-size: 16px;
  line-height: 32px;
  color: #111114;
  background-color: #fff;
}

.form-button {
  min-width: 120px;
  padding: 4px;
  border-radius: 0 4px 4px 0;
  font-size: 16px;
  line-height: 32px;
  text-transform: uppercase;
  color: #fff;
  background-color: #111114;

  &:disabled {
    background-color: rgba(0, 0, 0, 0.2);
    cursor: default;
  }
}
